import { Component, OnInit } from '@angular/core';
import { OpenPlaylistService } from "../../services/open-playlist.service";
import { LoadJsService } from "../../services/loadjs/load-js.service";

@Component({
  selector: 'app-right-sidebar',
  templateUrl: './right-sidebar.component.html',
  styleUrls: ['./right-sidebar.component.css']
})
export class RightSidebarComponent implements OnInit {

  constructor(private openPlaylistService: OpenPlaylistService, private loadJsService: LoadJsService) {
    // this.loadJsService.loadJs()
  }

  hasClicked: any = false;
  statusOfSongs : any = false;

  getStatus(statusData) {
    console.log('status :::::', statusData);
    this.openPlaylistService.updatetab(statusData)
  }

  showSideBar() {
    if (this.hasClicked) {
      this.hasClicked = false;
    } else {
      this.hasClicked = true;
    }
  }

  ngOnInit(): void {
    this.openPlaylistService.sharable.subscribe(
      status => {
        console.log('status of init :::::', status);
        this.statusOfSongs = status
      },
      err => {
        console.log('error in status of songs :::::', err);
      }
    )
  }

}
