import { Component, OnInit } from '@angular/core';
import { OpenPlaylistService } from '../../services/open-playlist.service';
import { LoginService } from '../../services/login/login.service';

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.css'],
})
export class AccountComponent implements OnInit {
  constructor(
    private openPlaylistService: OpenPlaylistService,
    private loginService: LoginService
  ) {}

  statusOfChat: any = false;
  showPersonal: any = false;
  showAccount: any = true;

  ngOnInit(): void {
    this.openPlaylistService.chatSharable.subscribe(
      (status) => {
        if (status == true) {
          this.showPersonal = false;
          this.showAccount = false;
        }
        this.statusOfChat = status;
      },
      (err) => {}
    );

    this.openPlaylistService.personalSharable.subscribe(
      (data) => {
        if (data == true) {
          this.statusOfChat = false;
          this.showAccount = false;
        }
        this.showPersonal = data;
      },
      (err) => {}
    );
  }
}
