import { Component, OnInit, Input } from '@angular/core';
import * as jwt_decode from 'jwt-decode';
import * as io from 'socket.io-client';
import { LoadJsService } from '../../../services/loadjs/load-js.service';
import { LoginService } from '../../../services/login/login.service';
import { UsersService } from '../../../services/users/users.service';

@Component({
  selector: 'app-chats-content',
  templateUrl: './chats-content.component.html',
  styleUrls: ['./chats-content.component.css'],
})
export class ChatsContentComponent implements OnInit {
  messageArray: any[] = [];
  usersArray: any[] = [];
  socket: any;

  constructor(
    private loginService: LoginService,
    private usersService: UsersService,
    public loadJsService: LoadJsService
  ) {
    this.socket = io.connect('http://localhost:3030');
  }

  message: any;
  address: any;
  output: any[] = [];
  username: any;
  user_id: any;
  socketAddress: any;
  showChatOption: any = false;
  reciever_id: any;

  sendMessage() {
    console.log(
      'all data of socket :::::',
      this.socketAddress,
      '------------------------',
      this.reciever_id,
      this.message
    );

    this.loginService.emit('chat', {
      address: this.socketAddress,
      user_id: jwt_decode(localStorage.getItem('user_token')).id,
      reciever_id: this.reciever_id,
      message: this.message,
    });
    this.output.push({ reply: this.message });
    this.message = '';
  }

  showChat(data, email, socketaddr, name, reciever_id) {
    this.showChatOption = data;
    this.username = name;
    this.socketAddress = socketaddr;
    this.reciever_id = reciever_id;
    var data_of_post = {
      user_id: this.user_id,
      reciever_id: reciever_id
    }
    this.output = []
    this.loginService.getAllChats(data_of_post).subscribe(
      data => {
        this.output = data.result;
      },
      err => {
        console.log('error :::::', err);
      }
    )
  }

  updateMessage(data) {
    this.message = '';
    console.log('data ----------------------------------:::::', data);
    
    if (!!!data) return;
    this.output.push(data);
  }

  updateFeedback(data: any) {
    this.message = data + 'typing a message';
  }

  messageTyping() {
    console.log('name :::::', this.username);
    this.loginService.emit('typing', this.username);
  }

  ngOnInit(): void {
    console.log('token :::::', jwt_decode(localStorage.getItem('user_token')));
    this.user_id = jwt_decode(localStorage.getItem('user_token')).id;

    setInterval(() => {
      this.loginService.sharable.subscribe(
        async (status) => {
          this.address = await this.loginService.socketId();
          console.log(this.address.id);
          
          var dataOfUpdate = {
            email: jwt_decode(localStorage.getItem('user_token')).id,
            socket_id: this.address.id,
          };
          console.log('data of up :::::', dataOfUpdate);
          
          this.loginService.updateSocket(dataOfUpdate).subscribe(
            (data) => {
              console.log('data of update :::::', data);
              this.usersService.getAllUsers().subscribe(
                (data) => {
                  this.usersArray = data.result;
                },
                (err) => {
                  console.log('err :::::', err);
                }
              );
            },
            (err) => {
              console.log('error in updat socket id :::::', err);
            }
          );
        },
        (err) => {
          console.log('error in status of songs :::::', err);
        }
      );
    }, 3000);

    this.loginService
      .listen('typing')
      .subscribe((data) => this.updateFeedback(data));
    this.loginService
      .listen('chat')
      .subscribe((data) => this.updateMessage(data));
  }
}
