import { Component, OnInit } from '@angular/core';
import { OpenPlaylistService } from "../../../services/open-playlist.service";

@Component({
  selector: 'app-account-left',
  templateUrl: './account-left.component.html',
  styleUrls: ['./account-left.component.css']
})
export class AccountLeftComponent implements OnInit {

  constructor(private openPlaylistService: OpenPlaylistService) { }

  getStatus(statusData) {
    this.openPlaylistService.updateChat(statusData)
  }

  getPersoalStatus(data) {
    this.openPlaylistService.updatePersonal(data)
  }

  ngOnInit(): void {
  }

}
